.app{
  background-color: black;
  color: antiquewhite;
  font-size: 14px;
}
.topText{
  margin: 1em 0em;
  font-size: 20px;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-weight: 500;
}
.main{
  text-align: center;
  padding: 2em 0;
}
.main input{
  border: none;
  border-radius: 5px;
  outline: none;
  padding: 5px;
}
.card{
  width: 90%;
  margin: 1em auto;
  background-color: darkgrey;
  color: antiquewhite;
  border: none;
  box-shadow: 2px 2px 30px rgb(143, 84, 84);
  padding: 10px;
}
.fa-user{
  font-size: 50px;
  width: 50px;
  color: darkslategray;
}
strong{
  color: darkblue;
}
span{
  color: darkslategray;
}
.fa-angle-double-down{
  padding: 0px;
  font-size: 30px;
  width: 30px;
  position: fixed;
  right: 10px;
  top: 400px;

}
.fa-angle-double-up{
  background-color: red;
  color: blue;
  padding: 5px;
  padding: 0px;
  font-size: 30px;
  width: 30px;
  border-radius: 5px;
  text-align: center;
  bottom: 0;
  left: 20px;
}

.card_details_2 p{
  display: flex;
  justify-content: space-between;
}
.nav{
  width: 100%;
  background-color: darkblue;

  position: fixed;
  right: -20;
  top: 25px;
  padding: 2em;
  display: none;
}
ul{
  list-style-type: none;
}
.sub_nav{
  display: flex;
  justify-content: space-evenly;
}
button{
  width: 60px;
  padding: 5px;
  color: #337AB7;
}

.fa-bars{
  font-size: 30px;
  position: fixed;
  top: 35px;
  right: 10px;
  z-index: 100;
}
input{
  color: black;
}
.subnav_btn{
  font-weight: bolder;
  font-size: 20px;
  color: cornsilk;
}
#footer{
  margin-top: 3em;
}
.navclose {
  display: block;
  width: 100%;
  background-color:#000093 ;
  color: aliceblue;
  position: fixed;
  right: 0;
  top: 25px;
  padding: 2em;
  -webkit-animation-name: navSmooth;
          animation-name: navSmooth;
  -webkit-animation-duration: 500ms;
          animation-duration: 500ms;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
}

@-webkit-keyframes navSmooth {
  from {
    width: 0%;
  }
  to {
    width: 100%;
  }
}

@keyframes navSmooth {
  from {
    width: 0%;
  }
  to {
    width: 100%;
  }
}



@media (min-width: 500px){
  .app{
    background-color: black;
    color: antiquewhite;
    font-size: 16px;
  }
  .card{
    width: 70%;
    margin: 1em auto;
    background-color: darkgrey;
    color: antiquewhite;
    border: none;
    box-shadow: 2px 2px 30px rgb(143, 84, 84);
    padding: 10px;
  }
  .card_details_2 p{
    display: flex;
    text-align: left;
    width: 70%;
    margin: 0 auto;
  }
  .card_details_2 p strong{
    width: 50%;
  }
  .card_details_2 p span{
    text-align: right;
  }


}